body {
  background-color: #D6E2FA;
  background-image: url('/img/bg.jpg');
  text-align: center;
  padding: 20px 32px;
  font-family: 'Montserrat', sans-serif;
  margin: 0;

  @media (max-width: 600px) {
    padding: 20px 12px;
  }
}

img {
  max-width: 100%;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  max-width: 1500px;
  margin: 0 auto;
}

.container.rules {
  display: block;
  max-width: 1000px;
  text-align: left;
  background-color: #fff;
  padding: 48px;
  margin-top: 50px;
  line-height: 1.4;
  @media (max-width: 600px) {
    padding: 24px 16px;
  }
}

.step {
  cursor: pointer;
  padding: 20px;
  box-sizing: border-box;

  &.disabled {
    filter: grayscale(100%);
    cursor: default;
  }

  .svg-wrapper {
    padding-bottom: 110%;
    position: relative;
    overflow: visible;
    transition: transform 300ms $cubic-bezier;

    svg {
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }

  @media (min-width: 1200px) {
    width: 25%;

    &:nth-child(2n),
    {
      position: relative;
      top: 160px;
    }
  }
  @media (min-width: 800px) and (max-width: 1200px) {
    width: 33%;
    &:nth-child(3n-1) {
      position: relative;
      top: 120px;
    }
  }
  @media (max-width: 800px) {
    width: 50%;
    margin-bottom: 50px;
  }

  @media (max-width: 600px) {
    width: 70%;
    margin-bottom: 30px;
  }

  &:hover .svg-wrapper {
    transform: scale(1) translateY(-20px);
  }
}



