body {
  background-color: #D6E2FA;
  background-image: url("/img/bg.jpg");
  text-align: center;
  padding: 20px 32px;
  font-family: "Montserrat", sans-serif;
  margin: 0;
}
@media (max-width: 600px) {
  body {
    padding: 20px 12px;
  }
}

img {
  max-width: 100%;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  max-width: 1500px;
  margin: 0 auto;
}

.container.rules {
  display: block;
  max-width: 1000px;
  text-align: left;
  background-color: #fff;
  padding: 48px;
  margin-top: 50px;
  line-height: 1.4;
}
@media (max-width: 600px) {
  .container.rules {
    padding: 24px 16px;
  }
}

.step {
  cursor: pointer;
  padding: 20px;
  box-sizing: border-box;
}
.step.disabled {
  filter: grayscale(100%);
  cursor: default;
}
.step .svg-wrapper {
  padding-bottom: 110%;
  position: relative;
  overflow: visible;
  transition: transform 300ms cubic-bezier(0.16, 1, 0.3, 1);
}
.step .svg-wrapper svg {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
}
@media (min-width: 1200px) {
  .step {
    width: 25%;
  }
  .step:nth-child(2n) {
    position: relative;
    top: 160px;
  }
}
@media (min-width: 800px) and (max-width: 1200px) {
  .step {
    width: 33%;
  }
  .step:nth-child(3n-1) {
    position: relative;
    top: 120px;
  }
}
@media (max-width: 800px) {
  .step {
    width: 50%;
    margin-bottom: 50px;
  }
}
@media (max-width: 600px) {
  .step {
    width: 70%;
    margin-bottom: 30px;
  }
}
.step:hover .svg-wrapper {
  transform: scale(1) translateY(-20px);
}

.title {
  color: #06c39f;
  font-size: 60px;
  margin-top: 40px;
  margin-bottom: 10px;
  text-transform: uppercase;
}
@media (max-width: 600px) {
  .title {
    font-size: 32px;
  }
}

.subtitle {
  max-width: 600px;
  margin: 0 auto;
  margin-bottom: 80px;
  font-size: 15px;
  line-height: 1.5;
  opacity: 0.7;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
}
.header .header__menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: auto;
}
@media (max-width: 600px) {
  .header.header--inner .header__menu {
    width: 100%;
  }
}
.header.header--inner .arrow-left {
  display: none;
}
@media (max-width: 600px) {
  .header.header--inner .arrow-left {
    display: block;
  }
}
@media (max-width: 600px) {
  .header.header--inner .header__logo {
    order: unset;
    display: none;
  }
}
@media (max-width: 600px) {
  .header.header--inner .logo-csc {
    display: none;
  }
}
.header .header__logo {
  margin-right: auto;
}
@media (max-width: 1000px) {
  .header .header__logo {
    width: 120px;
  }
}
@media (max-width: 600px) {
  .header .header__logo {
    width: 100%;
    order: 1;
    margin-top: 80px;
  }
}
.header .header__link {
  display: flex;
  align-items: center;
  font-size: 18px;
  font-weight: 900;
  text-decoration: none;
  color: #8886fc;
  margin-left: 24px;
}
.header .header__link:hover {
  color: #5e5ccf;
}
@media (max-width: 800px) {
  .header .header__link {
    font-size: 0;
    margin-left: 4px;
    width: 40px;
    height: 40px;
  }
}

.btn-start {
  text-decoration: none;
  font-weight: 900;
  cursor: pointer;
  margin-left: 20px;
  position: relative;
  top: -25px;
}
@media (max-width: 800px) {
  .btn-start {
    margin-left: 0;
  }
}

.side-right,
.side-left,
.side-top {
  display: inline-block;
  font-size: 15px;
  transition: transform 100ms linear;
  transform-origin: left top;
}

.side-right {
  transform: rotate(-10deg) skewX(-10deg) scaleY(0.864) translate(-3px, 44px);
  color: #fff;
  background-color: #fc6949;
  padding: 8px 12px;
}

.side-left {
  background-color: #fb504d;
  transform: rotate(32deg) skewX(32deg) scaleY(1.6) translate(5px, 11px);
  padding: 8px 8px;
}

.side-top {
  transform-origin: center;
  background-color: #fbae2f;
  transform: rotate(80deg) skewY(-49deg) scaleX(0.67) scaleY(8.1) translate(36px, -7px);
  padding: 8px 8px;
}

.btn-start:hover {
  filter: brightness(1.1);
}
.btn-start:hover .side-right {
  transform: rotate(-10deg) skewX(-10deg) scaleY(0.864) translate(-10px, 38px);
}
.btn-start:hover .side-left {
  transform: rotate(32deg) skewX(32deg) scaleY(1.6) scaleX(0.5) translate(10px, 11px);
}
.btn-start:hover .side-top {
  transform: rotate(80deg) skewY(-49deg) scaleX(0.35) scaleY(8.1) translate(59px, -7px);
}

/* The Modal (background) */
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1060;
  display: none;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  outline: 0;
  background-image: url("/img/pattern.png");
  background-color: rgba(0, 0, 0, 0.7);
  animation-name: shade;
  animation-duration: 0.2s;
}

/* Modal Content */
.modal-content {
  background-color: #ffffff;
  margin: 100px auto;
  padding: 0;
  max-width: 600px;
  animation-name: animatetop;
  animation-duration: 0.4s;
}

/* Add Animation */
@keyframes animatetop {
  from {
    transform: translateY(100px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes shade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* The Close Button */
.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.close:hover,
.close:focus {
  opacity: 0.7;
}

.modal-header {
  padding: 12px;
  background-color: #fbae2f;
  color: white;
  margin-bottom: 40px;
  position: relative;
}

.modal-body {
  padding: 2px 16px;
}

#start {
  animation: shake 2s cubic-bezier(0.36, 0.07, 0.19, 0.97) infinite;
  animation-delay: 500ms;
}
#start:hover, #start.stopped {
  animation: none;
}

#start #hand {
  transform: translateY(600px) translateX(150px) scale(0.3);
  transition: transform 600ms cubic-bezier(0.16, 1, 0.3, 1);
  transition-delay: 300ms;
}

#start:hover #hand {
  transform: translateY(0px) translateX(0px) scale(1);
  transition-delay: 100ms;
}

#start:hover #btn {
  transform: translateX(15px) translateY(-15px);
  transition: transform 600ms cubic-bezier(0.16, 1, 0.3, 1);
}

#start #flag {
  transform: translateY(280px) translateX(290px) scale(0);
  transition: all 200ms cubic-bezier(0.16, 1, 0.3, 1);
  transition-delay: 200ms;
}

#start:hover #flag {
  transform: translateY(0px) translateX(0px) scale(1);
  transition-delay: 790ms;
}

#start:hover #finger {
  animation: hand-anim-finger 400ms ease-in-out;
  animation-delay: 700ms;
  animation-iteration-count: 1;
  animation-direction: alternate-reverse;
}

@keyframes hand-anim-finger {
  from {
    transform: translateY(0) translateX(0);
  }
  to {
    transform: translateY(15px) translateX(-14px);
  }
}
@keyframes shake {
  5% {
    transform: translateX(-1px);
  }
  10%, 40% {
    transform: translateX(2px);
  }
  15%, 45% {
    transform: translateX(-2px);
  }
  20%, 30% {
    transform: translateX(4px);
  }
  25%, 35% {
    transform: translateX(-4px);
  }
  50% {
    transform: translateX(0);
  }
}